<template lang="pug">
  transition(name="fade")
    div.booking-popup(
      v-if="bookingPopup"
    )
      div.container
        div.booking-form
          form
            label(for="name") 
            input(
              type="text" 
              id="name" 
              placeholder=""
              v-model="name"
            )
            label(for="telephone") 
            input(
              type="text" 
              id="telephone" 
              placeholder=""
              v-model="telephone"
            )
            P.checkIn 
            DatePicker(
              :value="checkIn"
              @input="checkInHandler($event)"
              locale="en-US"
              :masks="{ weekdays: 'WW', L: 'YYYY - MM - DD' }"
              :available-dates='{ start: useCalculateDays(new Date()), end: null }'
            )
            p.checkOut 
            DatePicker(
              :value="checkOut"
              @input="checkOutHandler($event)"
              locale="en-US"
              :masks="{ weekdays: 'WW', L: 'YYYY - MM - DD' }"
              :available-dates='{ start: useCalculateDays(checkIn), end: null }'
            )
            p.days {{ selectedPeriodOfDays }}{{ normalDays }}
            div.price
              p 
              p ${{ totalPrice | currencyComma }}
            button(
              @click.prevent="emitBookingFormHandler"
            ) 
            p.note 
        div.booking-room-info
          div.room-description
            h2 
              span {{ singleRoomName }}
              hr
            p.room-specification {{ roomSpecification }}
            p.price {{ normalDayCost }} / {{ holidayCost }}
            div.amentities
              BookingAmenities(
                :roomAmentities="roomAmentities"
              )
          div.use-guide
            h3 
              span 
              hr 
            ul
              li 150021001000
              li 
              li 
              li  03-8321155 (   10:00 - 18:00 )
          BookingProcess 
        CrossButton(
          :crossSize="'sm'"
          :crossColor="'dark-green'"
          @click.native="closePupop"
        )

</template>

<script>
// plugin
import { mapActions, mapGetters } from "vuex";
import { 
  calculateDays,
  calculatePeriodOfDays,
  periodOfDays
} from '@/assets/utils/dateConvertor.js';
import {
  formatCurrency
} from '@/assets/utils/currencyConvertor.js';
import {
  getSelectedDays,
  calculateRoomPrice,
  calculatePrice
} from '@/assets/utils/aboutRoomCalculate.js'
// components
import CrossButton from '@/components/base/CrossButton.vue'
import ArrowButton from '@/components/base/ArrowButton.vue'
import DatePicker from 'v-calendar/lib/components/date-picker.umd'
import BookingAmenities from '@/components/pages/RoomsInfo/BookingAmenities.vue';
import BookingProcess from '@/components/pages/RoomsInfo/BookingPopup/BookingProcess.vue'
// views




export default {
  props: {
    bookingPopup: {
      type:Boolean,
      required: true
    },
    roomAmentities: {
      type: Object,
      required: true
    },
    checkOut: {
      type: Date,
      required: true
    },
    checkIn: {
      type: Date,
      required: true
    },
    normalDayCost: {
      type: Number,
      required: true
    },
    holidayCost: {
      type: Number,
      required: true
    },
    roomSpecification: {
      type:String,
      required: true
    },
    singleRoomName: {
      type:String,
      required: true
    },
  },
  data() {
    return {
      name: '',
      telephone: '',
    }
  },
  methods: {
    ...mapActions({

    }),
    closePupop() {
      this.$emit('propBookingPopup');
    },
    checkOutHandler(date) {
      this.$emit('propBookingPopupDateHandler', 'checkOut', date);
    },
    checkInHandler(date) {
      this.$emit('propBookingPopupDateHandler', 'checkIn', date);
    },
    useCalculateDays(checkIn) {
      return calculateDays(checkIn, 1);
    },
    emitBookingFormHandler() {
      const date = calculatePeriodOfDays(this.checkIn, this.checkOut);
      const id = this.$route.params.id;
      const postObj = {
        id,
        postParams: {
          name: this.name,
          tel: this.telephone,
          date,
        }
      };
      this.$emit('propBookingRoomHandler', postObj);
    },
  },
  computed: {
    ...mapGetters({

    }),
    totalPrice() {
      if(!this.normalDayCost || !this.holidayCost) return;
      const selectedDays = getSelectedDays(this.checkIn, this.checkOut);
      const normalDay = ['', '', '', ''], holidayDay = ['', '', ''];
      const normalDayPrice = calculateRoomPrice(normalDay, this.normalDayCost, selectedDays);
      const holidayDayPrice = calculateRoomPrice(holidayDay, this.holidayCost, selectedDays);
      const getTotalPrice = calculatePrice(holidayDayPrice, normalDayPrice);
      return getTotalPrice;
    },
    normalDays() {
      const normalDay = ['', '', '', ''];
      const day = getSelectedDays(this.checkIn, this.checkOut).reduce((total, day, index) => {
        let isNormalDay = normalDay.find(normal => normal == day);
        const lastIndex = getSelectedDays(this.checkIn, this.checkOut).length - 1;
        if (!isNormalDay || index == lastIndex) return total;
        
        total += 1;
        return total;
      }, 0);  
      return day;
    },
    selectedPeriodOfDays() {
      const selectedDays = getSelectedDays(this.checkIn, this.checkOut);
      return selectedDays.length;
    }
  },
  components: {
    CrossButton,
    DatePicker,
    BookingAmenities,
    BookingProcess,
    ArrowButton,
  },
  watch: {},
  filters: {
    currencyComma(value) {
      return formatCurrency(value);
    }
  },
  mounted() {}
}
</script>

